nav {
  width: 100vw;
  height: 10vh;
  background-color: #111;
  font-size: 2.5vw;
}

nav ul {
  width: 100%;
  display: flex;
  justify-content: space-around;
  line-height: 10vh;
}

nav ul li {
  flex-basis: 25%;
  text-align: center;
  display: inline-block;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  transition: .3s;
}

nav ul li:hover a {
  font-size: 1.1em;
  color: greenyellow;
}

@media (max-width: 414px) {
  nav {
    font-size: 5vw;
  }
}
