.AboutMeWrapper{width:100vw;height:90vh;display:flex;flex-direction:column;justify-content:space-between;background:#111}.AboutMeWrapper .title{display:flex;flex-direction:column;align-items:center;color:#fff}.AboutMeWrapper .title h1{font-size:4.5vw}.AboutMeWrapper .title h2{font-size:3.3vw}.AboutMeWrapper .Aboutme{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;text-align:center;padding:20px 50px;color:greenyellow;background-color:#111;font-size:23px;font-style:bold}.AboutMeWrapper .Aboutme p{flex-basis:40vw;margin-bottom:30px}@media (max-width: 414px){.AboutMeWrapper .title h1{font-size:8.5vw}.AboutMeWrapper .title h2{font-size:6.5vw}.AboutMeWrapper .Aboutme p{flex-basis:100vw}}
