.Contact{display:flex;justify-content:space-around;align-items:center;text-align:center;height:90vh;background-color:#111}.Contact span{flex-basis:30%}.Contact span i{color:#fff;transition:.3s;font-size:9.5vw}.Contact span i p{font-family:'Glory', sans-serif;transition:1s;font-size:2vw}.Contact span i:hover{color:greenyellow}.Contact span i:hover p{color:#fff}.Contact a{flex-basis:30%;color:#fff;transition:.3s}.Contact a i{font-size:9.5vw}.Contact a:hover{color:greenyellow}
