.projectWrapper{min-height:90vh;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;background-color:#111}.projectWrapper div{flex-basis:30%;display:flex;flex-wrap:wrap;justify-content:space-around;padding:15px}.projectWrapper div h1{margin-bottom:20px;color:#fff}.projectWrapper div img{width:100%;border:2px solid #fff;border-radius:10px;margin-bottom:10px;border:2px solid #fff;transition:.3s}.projectWrapper div a{text-decoration:none;padding:5px 15px;border:2px solid greenyellow;border-radius:10px;color:#fff;letter-spacing:1px;transition:.3s}.projectWrapper div a:hover{color:greenyellow}
